import request from '@/utils/request'

// 分页查询HCY信息
export function pageList(query) {
  return request({
    url: '/fixHcy/pageList',
    method: 'get',
    params: query,
  })
}

// 不分页查询HCY信息
export function list(data) {
  return request({
    url: '/fixHcy/list',
    method: 'post',
    data: data
  })
}

// 添加HCY信息
export function inserty(data) {
  return request({
    url: '/fixHcy/insert',
    method: 'post',
    data: data
  })
}

// 修改HCY信息
export function update(data) {
  return request({
    url: '/fixHcy/update',
    method: 'put',
    data: data,
  })
}

// 添加或修改HCY信息
export function savey(data) {
  return request({
    url: '/fixHcy/save',
    method: 'post',
    data: data
  })
}

// 删除HCY信息
export function del(id) {
  return request({
    url: '/fixHcy/'+id,
    method: 'delete'
  })
}

// 根据id查询HCY信息
export function onebyid(id) {
  return request({
    url: '/fixHcy/'+id,
    method: 'get'
  })
}
