<template>
  <div>
    <a-form layout="inline" :form="query" style="margin-bottom: 14px;">
      <a-form-item label="Model">
        <a-input v-model="query.model" disabled/>
      </a-form-item>
      <a-form-item label="Bulid">
        <a-select v-model="query.build" style="width:100px">
           <a-select-option value="">
            请选择
          </a-select-option>
          <a-select-option value="P1">
              P1
          </a-select-option>
          <a-select-option value="P2">
              P2
          </a-select-option>
          <a-select-option value="EVT">
              EVT
          </a-select-option>
          <a-select-option value="OVB">
              OVB
          </a-select-option>
          <a-select-option value="CRB">
              CRB
          </a-select-option>
          <a-select-option value="PRB">
              PRB
          </a-select-option>
          <a-select-option value="DVT">
              DVT
          </a-select-option>
          <a-select-option value="PPVT">
              PPVT
          </a-select-option>
          <a-select-option value="PVT-E">
              PVT-E
          </a-select-option>
          <a-select-option value="PVT">
              PVT
          </a-select-option>
          <a-select-option value="RAMP">
              RAMP
          </a-select-option>
          <a-select-option value="MP">
              MP
          </a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="Process">
        <a-input v-model="query.process" />
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 8, offset: 5 }">
        <a-button type="primary" html-type="submit" @click="findSubmit()" icon="search">查找</a-button>
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 8, offset: 3 }">
        <a-button html-type="submit" @click="add()" icon="plus">添加</a-button>
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 8, offset: 3 }">
                <a-upload
                    name="file"
                    :multiple="true"
                    :action="baseURL+'/fixHc/upload'"
                    :headers="headers"
                    @change="handleChange"
                >
                    <a-button> <a-icon type="upload" />上傳 </a-button>
                </a-upload>
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 8, offset: 3 }">
        <a-button html-type="submit" @click="exporthc()" icon="export">下載</a-button>
      </a-form-item>

    </a-form>
    <a-table :columns="columns" size="small" :data-source="data" rowKey="id">
      <span slot="action" slot-scope="record">
        <a @click="edit(record)"><a-icon type="edit" /></a>
        <a-divider type="vertical" />
        <a-popconfirm 
          v-if="role=='IE_1'||role=='admin'"
          title="您確定要刪除該數據嗎?"
          ok-text="Yes"
          cancel-text="No"
          @confirm="deluser(record.id)"
        >
        <a style="color:red"><a-icon type="delete" /></a>
        </a-popconfirm>
        <a-divider type="vertical" />
        <a style="color:green" @click="addfixture(record)"><a-icon type="plus" />Fixture</a>
      </span>
      <a-descriptions
        slot="expandedRowRender"
        slot-scope="record"
        style="margin: 0"
      >
        <a-descriptions-item label="Model">
          {{ record.model }}
        </a-descriptions-item>
        <a-descriptions-item label="Build">
          {{ record.build }}
        </a-descriptions-item>
        <a-descriptions-item label="Process">
          {{ record.process }}
        </a-descriptions-item>
        <a-descriptions-item label="UPH">
          {{ record.uph }}
        </a-descriptions-item>
        <a-descriptions-item label="Type">
          {{ record.type }}
        </a-descriptions-item>
        <a-descriptions-item label="No">
          {{ record.sno }}
        </a-descriptions-item>
        <a-descriptions-item label="Station Name">
          {{ record.sname }}
        </a-descriptions-item>
        <a-descriptions-item label="HC">
          {{ record.qpl }}
        </a-descriptions-item>
        <a-descriptions-item label="Lasttime">
          {{ record.lasttime }}
        </a-descriptions-item>
      </a-descriptions>
    </a-table>

    <!-- 添加或修改model -->
    <a-modal
      :title="title"
      :visible="visible"
      :zIndex="9"
      @ok="handleOk"
      @cancel="handleCancel"
      cancelText="关闭"
      okText="確定"
    >
      <a-form-model
        ref="ruleForm"
        :rules="rules"
        :model="formData"
        :label-col="{ span: 6 }"
        :wrapper-col="{ span: 16 }"
      >
        <a-form-model-item label="id"  prop="id" style="display:none">
          <a-input v-model="formData.id" />
        </a-form-model-item>
        <a-form-model-item label="Model" prop="model">
          <a-input v-model="formData.model" disabled/>
        </a-form-model-item>
        <a-form-model-item label="Build" prop="build">
          <a-input v-model="formData.build" />
        </a-form-model-item>
        <a-form-model-item label="Process" prop="process">
          <a-input v-model="formData.process" />
        </a-form-model-item>
        <a-form-model-item label="No" prop="sno">
          <a-input v-model="formData.sno" />
        </a-form-model-item>
        <a-form-model-item label="Station Name" prop="sname">
          <a-input v-model="formData.sname" />
        </a-form-model-item>
        <a-form-model-item label="QPL" prop="qpl">
          <a-input v-model="formData.qpl" />
        </a-form-model-item>
        <a-form-model-item label="CT">
          <a-input v-model="formData.ct" />
        </a-form-model-item>
     </a-form-model>
    </a-modal>

    <!-- 添加fixture model -->
    <a-modal
      title="添加fixture信息"
      :visible="fixvisible"
      :zIndex="9"
      @ok="fixhandleOk"
      @cancel="fixhandleCancel"
      cancelText="关闭"
      okText="確定"
    >
      <a-form-model
        ref="addfixForm"
        :rules="fixrules"
        :model="fixData"
        :label-col="{ span: 6 }"
        :wrapper-col="{ span: 16 }"
      >
        <a-form-model-item label="id"  prop="id" style="display:none">
          <a-input v-model="fixData.id" />
        </a-form-model-item>
        <a-form-model-item label="Build">
          <a-input v-model="fixData.build" disabled/>
        </a-form-model-item>
        <a-form-model-item label="Process">
          <a-input v-model="fixData.process" disabled/>
        </a-form-model-item>
        <a-form-model-item label="Station Name">
          <a-input v-model="fixData.sname" disabled/>
        </a-form-model-item>
        <a-form-model-item label="Fixture name"  prop="fname">
          <a-input v-model="fixData.fname"/>
        </a-form-model-item>
        <a-form-model-item label="QTY per station" prop="qtystation">
          <a-input v-model="fixData.qtystation"/>
        </a-form-model-item>
        <a-form-model-item label="Quantity per Line" prop="qpline">
          <a-input v-model="fixData.qpline"/>
        </a-form-model-item>
        <a-form-model-item label="Dep" prop="dept">
          <a-input v-model="fixData.dept"/>
        </a-form-model-item>
     </a-form-model>
    </a-modal>
  </div>
</template>
<script>
const columns = [
  { title: "Model", dataIndex: "model",width: 100},
  { title: "Build", dataIndex: "build",width: 80},
  { title: "Process", dataIndex: "process",width: 100},
  { title: "UPH", dataIndex: "uph",width: 80},
   { title: "Type", dataIndex: "type",width: 100},
  { title: "No", dataIndex: "sno" ,width: 100},
  { title: "Station Name", dataIndex: "sname",ellipsis: true},
  { title: "HC", dataIndex: "qpl",width: 70},
  {
    title: "操作",
    dataIndex: "",
    key: "x",
    width: 150,
    scopedSlots: { customRender: "action" },
  },
];

import { pageList, save,del,upload,exports} from "@/api/fix/hc";
import {inserty} from "@/api/fix/hcy";
import {insert} from "@/api/fix/list";
import {insertfy} from "@/api/fix/listy";
import { userInfo } from "@/api/system/user";
import security from '@/utils/security'
import request from '@/utils/request'
export default {
  data() {
    return {
      data: [],
      headers:{
          authorization:localStorage.getItem("token"),
      },
      baseURL:request.defaults.baseURL,
      role:security.getUser(),
      columns,
      query: {
        limit: 50,
        page: 1,
        model: localStorage.getItem("model"),
        build: "",
        process: "",
      },
      title: "HC修改",
      visible: false,
      fixvisible: false,
      delvisible: false,
      fixData:{
        lasttime:""
      },
      formData: {
        model: localStorage.getItem("model"),
        build: "",
        process: "",
        sno: "",
        sname: "",
        qpl: "",
        ct: "",
        remark: "",
        lasttime: ""
      },
      rules: {
        id: [
          { required: false },
        ],
        model: [
          { required: true, message: "請輸入幾種名稱", trigger: "change" },
        ],
        build: [{ required: true, message: "請輸入階段", trigger: "change" }],
        process: [
          { required: true, message: "請輸入process", trigger: "change" },
        ],
        sno: [{ required: true, message: "請輸入no", trigger: "change" }],
        sname: [{ required: true, message: "請選擇站位名稱", trigger: "change" }],
        qpl: [
          { required: true, message: "請輸入QPL", trigger: "change" },
        ],
      },
      fixrules:{
        fname: [{ required: true, message: "請输入fixture name", trigger: "change" }],
        qpline: [{ required: true, message: "請输入Quantity per Line", trigger: "change" }],
        qtystation: [{ required: true, message: "請输入QTY per station", trigger: "change" }],
        dept: [{ required: true, message: "請输入部门", trigger: "change" }],
      }
    };
  },
  methods: {
    findSubmit() {
      pageList(this.query).then((res) => {
        this.data = res.data;
      });
    },
    edit(record) {
      this.title="HC修改"
      this.visible = true;
      setTimeout(()=>{
        this.$refs.ruleForm && (this.$refs.ruleForm.resetFields() || this.$refs.ruleForm.clearValidate())
        Object.assign(this.formData,record)
      })
    },
    deluser(id) {
      del(id).then((res) => {
        if (res.code == 200) {
          this.$message.success("刪除成功");
          this.findSubmit()
        }
      });
    },
    add(){
      this.title="HC添加"
      this.visible = true
      this.$refs.ruleForm && (this.$refs.ruleForm.resetFields() || this.$refs.ruleForm.clearValidate())
    },
    //添加或修改提交
    handleOk() {
      this.formData.lasttime= this.format(new Date(),'yyyy-MM-dd HH:mm:ss');
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          //發送添加或修改請求
          if(this.role=="admin" || this.role=="IE_1"){
            save(this.formData).then((res) => {
                if (res.code == 200&&this.formData.id===undefined) {
                this.$message.success("添加成功");
                }
                else{
                this.$message.success("修改成功");
                }
                this.visible = false;
                this.findSubmit()
            });
          }
          //發送預添加或預修改請求
          else{
            if(this.formData.id!==undefined){
              this.formData.hcid=this.formData.id;
              this.formData.id=""
            }
            inserty(this.formData).then((res) => {
                if (res.code == 200&&this.formData.hcid===undefined) {
                this.$message.success("預添加成功,請等待確認");
                }
                else{
                this.$message.success("預修改成功,請等待確認");
                }
                this.visible = false;
                this.findSubmit()
            });
          }
        }
        else{
           return false;
        }
      })
    },
    handleCancel() {
      this.visible = false;
      this.findSubmit()
    },
    //文件上傳
    handleChange(info) {
      if (info.file.status === 'done') {
        this.$message.success(`${info.file.name} 文件上傳成功`);
      } else if (info.file.status === 'error') {
        this.$message.error(`${info.file.name} file upload failed.`);
      }
    },
    exporthc(){

        exports(this.formData.model).then((res) => {
          var blob = res;// 获取返回值
          var a = document.createElement('a');
          a.href = window.URL.createObjectURL(blob);
          // var date=new Date();
          a.download ="HC信息表";
          a.click();
        });
    },
    addfixture(msg){
      this.fixvisible=true
      this.fixData=msg;
    },
    fixhandleCancel() {
      this.fixvisible = false;
    },
    fixhandleOk() {
      this.$refs.addfixForm.validate(valid => {
        if (valid) {
          this.fixData.lasttime= this.format(new Date(),'yyyy-MM-dd HH:mm:ss');
          this.fixData.hcid=this.fixData.id
          this.fixData.id="";
          //直接修改
          if(this.role=="admin" || this.role=="IE_1"){
            insert(this.fixData).then((res) => {
                if (res.code == 200) {
                this.$message.success("fixture添加成功");
                }
            });
          }
          //预修改
          else{
              insertfy(this.fixData).then((res) => {
                  if (res.code == 200) {
                  this.$message.success("fixture預添加成功,請等待確認");
                  }
              });
          }
          this.fixData.fname="";
          this.fixData.qtystation="";
          this.fixData.qpline="";
          this.fixData.dept="";
        }
        else{
          return false
        }
      })
    },
    /**
     * 这两个函数是为了得到前台的国际化时间，便于后台进行操作
     */
    format(time, format) {   //前台获取时间，转化为国际时间，可以当做工具使用
      var t = new Date(time);
      var tf = function(i){return (i < 10 ? '0' : "") + i};
      return format.replace(/yyyy|MM|dd|HH|mm|ss/g, function(a){
          switch(a){
              case 'yyyy':
                  return tf(t.getFullYear());
                  break;
              case 'MM':
                  return tf(t.getMonth() + 1);
                  break;
              case 'mm':
                  return tf(t.getMinutes());
                  break;
              case 'dd':
                  return tf(t.getDate());
                  break;
              case 'HH':
                  return tf(t.getHours());
                  break;
              case 'ss':
                  return tf(t.getSeconds());
                  break;
          }
      })
  }
  },
  created: function () {
    pageList(this.query).then((res) => {
      this.data = res.data;
    });
  },
};
</script>